@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Roboto+Condensed:wght@300&display=swap);
:root {
  --color-primary: #7380ec;
  --color-danger: #ff7782;
  --color-success: #41f1b6;
  --color-warning: #ffbb55;
  --color-white: #fff;
  --color-info-dark: #7d8da1;
  --color-info-light: #dce1eb;
  --color-dark: #363949;
  --color-light: #rgba(132, 139, 200, 0.18);
  --color-primary-variant: #111e88;
  --color-dark-variant: #677483;
  --color-background: #f6f6f9;
}

body {
  font-family: "Roboto Condesed", sans-serif;
}

/******* NAVBAR LINKS *********/

nav {
  z-index: 2;
}
nav a.nav-link {
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--color-white) !important;
  border-top: 0.1875rem solid #363949;
  border-top: 0.1875rem solid var(--color-dark);
} 

.navbar-nav > li > a:hover {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--color-white) !important;
  border-top: 0.1875rem solid #ffbb55;
  border-top: 0.1875rem solid var(--color-warning);
} 

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #ffbb55;
  border-top: 0.1875rem solid var(--color-warning);
}

.navbar-brand {
  font-family: "Libre Baskerville" ;
  color: #ffbb55 !important;
  color: var(--color-warning) !important;
}

.navbar-toggler-icon {
  color: #fff !important;
  color: var(--color-white) !important;
}


/******* HEADER *********/

.header-wraper {
  position: relative;
  background: url(/My_Resume/static/media/background33.50a207ee.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Libre Baskerville", sans-serif;
  z-index: 1;
}

.btn-main-offer:hover {
  color: #ffbb55;
  color: var(--color-warning);

}

.main-info h1 {
  color: #ffbb55;
  color: var(--color-warning);
  font-size: 2.5rem;
  padding-bottom: 10px;
  border-bottom: 3px solid #e3e3eb;
  letter-spacing: 5px;
}

.main-info h2 {
  font-size: 2rem;
  padding-top: 5px;
  color: #ffbb55;
  color: var(--color-warning);
  font-family: "Libre Baskerville", sans-serif;
  
}

/******** TYPED TEXT *******/

.typed-text {
  padding-top: 10px;
  font-size: 2rem;
  color: #fff;
  color: var(--color-white);
}

/******** MAIN OFFER BUTTON *******/

.btn-main-offer {
  border: 1px solid #ffbb55;
  border: 1px solid var(--color-warning);
  text-decoration: none;
  text-transform: uppercase;
  border-radius: 0;
  padding: 10px 12px;
  color: #fff;
  color: var(--color-white);
  margin-top: 2rem;
}

/******** PARTICLES JS *******/

/* ---- reset ---- */


/* ---- tsparticles container ---- */
#tsparticles {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #0d48a100;
  background-image: url("");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  z-index: 1;
}

/*ANimation stuff*/
.main-info {
  -webkit-animation-name: zoomIn;
          animation-name: zoomIn;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes zoomIn {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    -webkit-filter: blur(10px);
            filter: blur(10px);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes zoomIn {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    -webkit-filter: blur(10px);
            filter: blur(10px);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
